<template>
  <section class="field-card">
    <header class="field-card__header">
      <div>
        <h2>{{ group.title }}</h2>
        <p v-if="group.description">{{ group.description }}</p>
      </div>
      <span class="field-card__badge">共 {{ group.fields.length }} 项</span>
    </header>

    <div
      class="field-card__grid"
      :style="{ gridTemplateColumns: `repeat(${group.columns ?? 1}, minmax(0, 1fr))` }"
    >
      <label
        v-for="field in group.fields"
        :key="field.path"
        class="field-card__item"
      >
        <span class="field-card__label">
          {{ field.label }}
          <small v-if="field.note">{{ field.note }}</small>
        </span>

        <input
          v-if="field.type !== 'textarea'"
          :type="field.type === 'number' ? 'number' : 'text'"
          :value="formState[field.path] ?? ''"
          :placeholder="field.placeholder"
          :min="field.type === 'number' ? field.min ?? 0 : undefined"
          :max="field.type === 'number' ? field.max ?? undefined : undefined"
          @input="onInput(field.path, ($event.target as HTMLInputElement).value)"
        />

        <textarea
          v-else
          :value="formState[field.path] ?? ''"
          :placeholder="field.placeholder"
          rows="3"
          @input="onInput(field.path, ($event.target as HTMLTextAreaElement).value)"
        />
      </label>
    </div>
  </section>
</template>

<script setup lang="ts">
import type { FieldGroup } from '../fieldSchema';

interface Props {
  group: FieldGroup;
  formState: Record<string, string>;
}

defineProps<Props>();

const emit = defineEmits<{
  (event: 'update:field', path: string, value: string): void;
}>();

function onInput(path: string, value: string) {
  emit('update:field', path, value);
}
</script>

