<template>
  <div class="opening-panel">
    <header class="opening-panel__hero">
      <h1>开局设定</h1>
    </header>

    <div class="panel-grid">
      <FieldGroupCard
        v-for="group in fieldGroups"
        :key="group.key"
        :group="group"
        :form-state="formState"
        @update:field="updateField"
      />
    </div>

    <footer class="panel-footer">
      <div class="panel-footer__messages">
        <p v-if="errorMessage" class="feedback feedback--error">{{ errorMessage }}</p>
        <p v-if="confirmation" class="feedback feedback--success">{{ confirmation }}</p>
      </div>
      <button :disabled="!canSubmit" @click="handleSubmit">
        {{ isSubmitting ? '提交中...' : '开始游戏' }}
      </button>
    </footer>
  </div>
</template>

<script setup lang="ts">
import FieldGroupCard from './components/FieldGroupCard.vue';
import { fieldGroups } from './fieldSchema';
import { useOpeningPanel } from './composables/useOpeningPanel';

const {
  formState,
  isReady,
  isSubmitting,
  errorMessage,
  confirmation,
  canSubmit,
  handleSubmit,
} = useOpeningPanel(fieldGroups);

function updateField(path: string, value: string) {
  formState[path] = value;
}
</script>

